const subscribe = (templateId) => {
    return new Promise((resolve, reject) => {

        if(wx.requestSubscribeMessage) {
            wx.requestSubscribeMessage({
                tmplIds: templateId,
                success (res) {
                    resolve(res);
                },
                fail(res) {
                    reject(res);
                }
            })
        } else {
            uni.showModal({
                title: '',
                content: '',
                showCancel: false,
                success(res) {
                    reject({});
                }
            });
        }




    });
};
export default subscribe;
